
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #F4F6F9;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden;
}


.video-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; 
  object-fit: cover;
}

.app-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 20px;
}



.app-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 20px;
}

.column {
  background-color: rgba(255, 255, 255, 0.6); 
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  width: 300px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  min-height: 400px;
}


h1,
h2 {
  color: #002B5C;
  text-align: center;
}

input {
  padding: 10px;
  border: 2px solid #A6A9AB;
  border-radius: 4px;
  width: 100%;
  margin-bottom: 10px;
}

button.add-task {
  padding: 10px;
  background-color: #002B5C;
  border: none;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  width: 100%;
}

button.add-task:hover {
  background-color: #001F43;
}

ul {
  list-style-type: none;
  padding: 0;
  flex-grow: 1;
}

li {
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
}

button.delete-task,
button.done-task,
button.doing-task {
  background-color: #002B5C;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 10px;
}

button.delete-task:hover,
button.done-task:hover,
button.doing-task:hover {
  background-color: #001F43;
}

button.doing-task {
  background-color: #FFCC00;
  color: #002B5C;
}

button.doing-task:hover {
  background-color: #e6b800;
}

.timer-container {
  margin-top: 20px;
  text-align: center;
  color: white;
  font-weight: 500;
}

.timer p {
  font-size: 2em;
  margin: 10px 0;
}

.start-stop-btn,
.reset-btn {
  padding: 10px;
  background-color: #002B5C;
  border: none;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  width: 100%;
}

.start-stop-btn:hover,
.reset-btn:hover {
  background-color: #001F43;
}

.reset-btn {
  background-color: #ff5330;
  color: #ffffff;
}

.column h2 {
  margin-top: 0;
}
